/** Base Styles **/
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before,
q:after {
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input,
select {
  vertical-align: middle;
}

audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}

audio:not([controls]) {
  display: none;
}

[hidden] {
  display: none;
}

/*

Usage:

@include placeholder {
    color: rgba($white, .8);
}

*/
/** Base Typography **/
body {
  color: #000;
  font-family: "Open Sans", sans-serif;
}

a,
a:visited {
  text-decoration: none;
}
a,
a:link,
a:hover,
input[type=submit],
input[type=submit]:hover,
.transition,
form .select .select-options li,
form .select .select-styled::before,
form .select .select-styled,
form input[type=radio] + label::before,
form input[type=radio] + label::after,
form input[type=checkbox] + label::before,
form input[type=checkbox] + label::after,
.transition:hover {
  transition: all 0.3s ease;
}

.cf {
  *zoom: 1;
}
.cf:before, .cf:after {
  content: " ";
  display: table;
}
.cf:after {
  clear: both;
}

.no-select, form .select .select-options li, form input[type=radio] + label, form input[type=checkbox] + label {
  user-select: none;
}

.h100 {
  height: 100%;
}

.rel {
  position: relative;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%;
}
.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

h4, h3, h2, h1 {
  font-family: "Open Sans", sans-serif;
}

p {
  font-size: 16px;
  line-height: 26px;
  margin-bottom: 30px;
}

/** Layouts **/
.navigation {
  display: inline-block;
  font-size: 0;
  float: right;
}
.navigation ul {
  list-style-type: none;
}
.navigation ul li {
  display: inline-block;
}
.navigation a {
  font-size: 16px;
  line-height: 30px;
  display: block;
  padding: 0 10px;
}
.navigation .dropdown {
  position: relative;
}
.navigation .dropdown-wrapper {
  display: none;
  position: absolute;
  top: 30px;
  right: 0;
}
.navigation .dropdown-wrapper li {
  display: block;
  border-bottom: 1px solid rgba(0, 0, 0, 0.6);
}
.navigation .dropdown-wrapper li:last-child {
  border-bottom: 0;
}

/** Components **/
.buttons {
  margin: 0 -10px 10px;
}
.buttons .button {
  position: relative;
  display: inline-block;
  margin: 0 10px 20px;
}

/*

Usage:

<div class="buttons>
    <a href="#" class="button">
    <a href="#" class="button">
</div>

*/
.all-inputs, form .select, form textarea, form fieldset, form input {
  margin-bottom: 20px;
}

form label,
form legend {
  cursor: pointer;
}
form input[type=radio], form input[type=checkbox] {
  display: none;
}
form input[type=radio]:checked + label::after, form input[type=checkbox]:checked + label::after {
  opacity: 1;
}
form input[type=radio] + label, form input[type=checkbox] + label {
  position: relative;
}
form input[type=radio] + label::before, form input[type=radio] + label::after, form input[type=checkbox] + label::before, form input[type=checkbox] + label::after {
  display: block;
  position: absolute;
  content: "";
  border: 2px solid red;
  background-color: white;
}
form input[type=radio] + label::after, form input[type=checkbox] + label::after {
  opacity: 0;
}
form input[type=radio] + label::before, form input[type=radio] + label::after {
  border-radius: 50%;
}
form fieldset legend + div > div {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-left: -10px;
  margin-right: -10px;
}
form fieldset legend + div > div > * {
  margin-left: 10px;
  margin-right: 10px;
}
form .select {
  position: relative;
  z-index: 1;
}
form .select .select-hidden {
  width: 0;
  height: 0;
  border: none;
  position: absolute;
}
form .select .select-styled {
  position: relative;
  cursor: pointer;
}
form .select .select-styled::before {
  display: block;
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 8px solid red;
  right: 12px;
  top: 14px;
}
form .select .select-styled.active::before {
  transform: rotate(-180deg);
}
form .select .select-options {
  display: none;
  position: absolute;
  list-style-type: none;
  cursor: pointer;
}
form .select .select-options li.hide {
  display: none;
}

